/* eslint-disable jsx-a11y/label-has-for */
import React, { PureComponent } from 'react';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { objectOf, any, func, bool, string as stringType } from 'prop-types';
import { withFormik } from 'formik';
import { object, string } from 'yup';
import { Input, Button } from 'homecredit-ui';
import { withStyles } from 'components/WithStyle';
import Layout from 'components/pz/PzLayout';
import { login } from './action';
import style from './Login.css';

const schema = object().shape({
  username: string().required('Thông tin này là bắt buộc!'),
  password: string().required('Thông tin này là bắt buộc!'),
});

class Login extends PureComponent {
  static propTypes = {
    values: objectOf(any).isRequired,
    handleSubmit: func,
    handleChange: func,
    handleBlur: func,
    touched: objectOf(bool),
    errors: objectOf(stringType),
    doLogin: func.isRequired,
    loginMessage: stringType.isRequired,
    loading: bool.isRequired,
  };

  static defaultProps = {
    handleSubmit: () => {},
    handleChange: () => {},
    handleBlur: () => {},
    touched: {},
    errors: {},
  };

  customHandleSubmit = values => e => {
    e.preventDefault();
    this.props.doLogin(values);
  };

  render() {
    const {
      values,
      handleSubmit,
      handleChange,
      handleBlur,
      touched,
      errors,
      loading,
    } = this.props;

    return (
      <Layout hideNextButton hideBackButton>
        <form
          className={style.loginForm}
          onSubmit={this.customHandleSubmit(values, handleSubmit)}
        >
          <h4 className={style.loginHeader}>Đăng nhập</h4>
          <Input
            label="Tên đăng nhập"
            id="username"
            name="username"
            onChange={handleChange}
            onBlur={handleBlur}
            touched={touched.username}
            errorMsg={errors.username}
            className={style.loginField}
            value={values.username}
          />
          <Input
            label="Mật khẩu"
            id="password"
            name="password"
            type="password"
            onChange={handleChange}
            onBlur={handleBlur}
            touched={touched.password}
            errorMsg={errors.password}
            className={style.loginField}
            value={values.password}
          />
          <div className={style.loginMessages}>
            <span>{this.props.loginMessage}</span>
            {this.props.loginMessage && (
              <i className="material-icons">warning</i>
            )}
          </div>
          <Button
            type="submit"
            className={style.loginSubmit}
            disabled={loading}
          >
            Đăng nhập
          </Button>
          <p className={style.loginTooltip}>
            Vui lòng liên hệ DSM để được hỗ trợ về truy cập tài khoản (nếu có)
          </p>
        </form>
      </Layout>
    );
  }
}

const mapStateToProps = state => ({
  loginMessage: state.pages.login.message,
  loading: state.pages.login.loading,
});

const mapDispatchToProps = dispatch => ({
  doLogin(values) {
    dispatch(login(values));
  },
});

const enhance = compose(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  ),
  withStyles(style),
  withFormik({
    mapPropsToValues: () => ({
      username: '',
      password: '',
    }),
    validationSchema: schema,
    displayName: 'Login',
  }),
);

export default enhance(Login);
